import React from 'react'
import EditIcon from '@material-ui/icons/Edit';
import {Link, Redirect} from 'react-router-dom'
import firebase from '../firebase'
import '../css/Profile.css'
import { Card } from 'react-bootstrap';

function Profile(props) {
    const user = props.user
    const loading = props.loading
    console.log(user)

    if(firebase.auth().currentUser === null){
        return <Redirect to="/"/>;
    }

    if(loading){
        return<h1>Loading...</h1>;
    }
    return (
        <div className="profile">
          <div className="profile__card">
          <h1>My Profile</h1>
            <div className="profile__content">
            <div className="profile__display">
            <img title="User's Image" src={user.photoURL}></img>
            <h1  title={user.name}>{user.name}</h1>
            <h2  title={user.email}>Email: {user.email}</h2>
            <h6  title={user.phone}>Phone: {user.phone}</h6>
            <h6  title={user.address}>Address: {user.address}</h6>
            </div>
            <hr/>
            <div className="profile__edit__button"> 
            <Link to="/profile/edit"><button title="Edit Info" type="button" className="btn btn-dark"><EditIcon/>Edit</button></Link>
            </div>
            </div>
          </div>
          <div className="profile__orders profile__card">
              <h1>My Order's</h1>
          </div>
        </div>
    )
}

export default Profile
