import React from 'react'
import './css/Footer.css'
import {Link} from 'react-router-dom'
import { signInWithGoogle } from './firebase'

function Footer() {
    return (
        <footer className="footer">
            <div className="footer-left">
                <img className="img" src="https://cutewallpaper.org/21/nike-logo-transparent-background/White-Nike-Logo-Png-Pictures-trzcacak.rs.png" alt=""></img>
                <p>Note: All the products available are imported first copies. All products are a replica of the original product. We assure you about the quality and look's of these products. The prices are acceptable according to the quality delivered.</p>
                <div className="socials">
                    <a href="https://www.facebook.com/nike/"><img className="svg" src="https://www.flaticon.com/svg/static/icons/svg/21/21155.svg"/></a>
                    <a href="https://twitter.com/nike"><img className="svg" src="https://www.flaticon.com/svg/static/icons/svg/733/733635.svg"/></a>
                    <a href="https://www.youtube.com/user/nike"><img className="svg" src="https://www.flaticon.com/svg/static/icons/svg/1384/1384028.svg"/></a>
                    <a href="https://www.instagram.com/nike"><img className="svg" src="https://www.flaticon.com/svg/static/icons/svg/1384/1384031.svg"/></a>
                </div>
            </div>
            <ul className="footer-right">
                <li>
                <Link to="/products"><h2 className="products">Products</h2></Link>
                    <ul className="box">
                        <li><Link to="/products/q=sneakers"><a href="">Sneaker's</a></Link></li>
                        <li><Link to="/products/q=shoe"><a href="">Shoes's</a></Link></li>
                        <li><Link to="/products/q=running"><a href="">Running's</a></Link></li>
                        <li><Link to="/products/q=basketball"><a href="">Basketball's</a></Link></li>
                        <li><Link to="/products/q=tennis"><a href="">Tennis's</a></Link></li>
                    </ul>
                </li>
                <li className="features">
                    <h2>Useful Links</h2>
                    <div>
                    <ul className="box">
                        <li><a><Link to="/about">About Us</Link></a></li>
                        <li><a><Link to="/contact">Contact</Link></a></li>
                        <li><a onClick={signInWithGoogle}><Link>Signup</Link></a></li>
                        <li><a>Pages</a></li>
                    </ul>
                    </div>
                </li>
            </ul>
            <div className="footer-bottom">
                <p>&copy;{new Date().getFullYear()} All Rights Reserved, Yeezy Inc.</p>
            </div>
        </footer>
    )
}

export default Footer
