import React, { Component } from 'react'
import Menu from './svg/bars-solid.svg'
import CartIcon from './svg/bag.svg'
import Close from './svg/times-solid.svg'
import Logo from '../imgs/Logo.png'
import {Link} from 'react-router-dom'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import './css/Header.css'
import firebase, { logout, signInWithGoogle } from './firebase'


export class Header extends Component {    
        state ={
            toggle: false,
            isLogin: false
        }


    
    componentDidMount () {
        let self = this
        firebase.auth().onAuthStateChanged(function(user){
            if(user){
                self.setState({ isLogin: true})
                console.log(user.displayName + " signed in.")
                
            }else {
                console.log("No User signed in")
                self.setState({isLogin: false})
            }
        })
    }

    menuToggle = ()=>{
        this.setState({toggle: !this.state.toggle})
    }
    

    render() {
        const {toggle} = this.state;
        return (
            <header>
                <div className="header__top">
                    <div>
                        <h6><a>10%</a> Discount with App</h6>
                    </div>
                <h6>Download App <Link to="/app"><a title="Get App">Here</a></Link></h6>
                {this.state.isLogin ? 
                <div>
                    <h6><a title="logout" onClick={logout}><ExitToAppIcon/></a></h6>
                </div>:<div className="signup__login">
                    <h6 onClick={signInWithGoogle}><PersonAddIcon/></h6>
                </div>}
                
                </div>
                <div className="header">    
                <div className="menu" onClick={this.menuToggle}>
                    <img src={Menu} alt="" width="20"/>
                 </div>
                 <div className="logo">
                 <Link to="/"><img title="Nike Home" src={Logo} style={{padding: '5px'}} width="140"></img></Link>
                 </div>
                 <nav>
                    <ul className={toggle ? "toggle": ""}>
                        <li onClick={this.menuToggle}><Link title="All Products" to="/products">Products</Link></li>
                        <li onClick={this.menuToggle}><Link title="Sneakers" to="/products/q=sneakers">Sneakers</Link></li>
                        <li className="close" onClick={this.menuToggle}>
                            <img src={Close} alt="" width="20"/>
                        </li>
                    </ul>
                    </nav>
                    <div className="nav-cart">
                        {
                            this.state.isLogin ?
                            <Link to="/profile"><img title="Profile" style={{borderRadius:"22px", height:"45px",margin:"5px"}} src={this.props.user}></img></Link>:
                            <></>
                        }
                        <Link to="/cart">
                        <span>0</span>
                        <img title="Cart" src={CartIcon} alt="" width="28"></img>
                        </Link>
                    </div>
                </div>
                
            </header>
        )
    }
}

export default Header
