import React, {useState } from 'react'
import firebase,{db} from '../firebase'
import '../css/ProfileEdit.css'
import { useHistory } from 'react-router-dom'


function ProfileEdit(props) {
    const history = useHistory()
    var user = props.userData
    const [name, setName] = useState(user.name)
    const [phone, setPhone] = useState(user.phone)
    const [address, setAddress] = useState(user.address)
    console.log(name)
    console.log(phone)
    console.log(address)

    function handleName(e){
        setName(e.target.value)
        e.preventDefault()
    }
    function handlePhone(e){
        setPhone(e.target.value)
        e.preventDefault()
    }
    function handleAddress(e){
        setAddress(e.target.value)
        e.preventDefault()
    }
    function updateData(e){
        e.preventDefault()
        db
        .collection("Users")
        .doc(user.uid)
        .update({
            name: name,
            phone: phone,
            address: address
        })
        alert("Profile Updated !!")
        history.push('/profile')
        
    }

    function discard(){
        alert("Dicard Changes ?")
        history.push('/profile')
    }
    

    return (
        <div className="profile__edit">
            <h1>Profile Edit</h1>
            <form onSubmit={updateData}>
            <div class="form-group row">
             <label class="col-sm-2 col-form-label">Name</label>
              <div class="col-sm-10">
              <input type="text" class="form-control" id="inputPassword" placeholder={user.name} onChange={handleName}/>
             </div>
            </div>
            <div class="form-group row">
             <label class="col-sm-2 col-form-label">Phone</label>
              <div class="col-sm-10">
              <input type="number" class="form-control" id="inputPassword" placeholder={user.phone} onChange={handlePhone}/>
             </div>
            </div>
            <div class="form-group row">
             <label for="staticEmail" class="col-sm-2 col-form-label">Address</label>
              <div class="col-sm-10">
              <input type="text" class="form-control" id="inputPassword" placeholder={user.address} onChange={handleAddress}/>
             </div>
            </div>
            <div className="form-group row">
            <input onClick={updateData} className="btn btn-dark col-sm-2 col-form-label" type="submit"/>
            </div>
            </form>
            <button onClick={discard} className="btn btn-danger col-form-label">Discard</button>
        </div>
    )
}

export default ProfileEdit


