import React from 'react'
import { useParams } from 'react-router-dom'
import '../css/Details.css'

function Details(props) {
    const products = props.products
    const params = useParams();
    const item = products.find(element => element.name === params.id)

    return (
        <div className="detail__page">
                <div className="detail__scroll">
            <div className="detail__img">
                    <div className="img"><img src={item ? item.Images[0] : ""}></img></div>
                    <div className="img"><img src={item ? item.Images[1] : ""}></img></div>
                    <div className="img"><img src={item ? item.Images[2] : ""}></img></div>
                    <div className="img"><img src={item ? item.Images[3] : ""}></img></div>
                </div>
            </div>
                <div className="detail__info">
                    <h1>{item.name}</h1>
                    <span>₹ {item.price - (10/100)*item.price}</span>
                    <h6>incl. of taxes and duties</h6>
                    <h4>{item.desc}</h4>
                    <button className="detail__buynow">Buy Now</button>
                    <button className="detail__button">Add to Cart</button>
                </div>
        </div>
    )
}


export default Details
