import React, {useState,useEffect} from 'react'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import firebase,{auth} from './components/firebase'
import Details from './components/section/Details'
import Products from './components/section/Products'
import Footer from './components/Footer'
import Header from './components/Header'
import About from './components/section/About'
import './App.css'
import Getapp from './components/section/Getapp';
import Search from './components/section/Search';
import Profile from './components/section/Profile';
import ProfileEdit from './components/section/ProfileEdit';

function App() {
  const[products, setProducts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState([]);

  const ref = firebase.firestore().collection("Products");


  function getProducts(){
      setLoading(true);
      ref.onSnapshot((querySnapshot)=>{
          const items = [];
          querySnapshot.forEach((doc)=>{
              items.push(doc.data());
          });
          setProducts(items);
          setLoading(false);
      })
  }


  useEffect(()=>{
      getProducts();
      firebase.auth().onAuthStateChanged(function(user){
        if(user){
          setLoading(true);
          var uid = user.uid
          const userRef = firebase.firestore().collection("Users").doc(uid)
          userRef.get().then(function(doc){
            if(doc.exists){
              const items = [];
              setUser(doc.data())
              console.log(doc.data())
              setLoading(false)
            }else {
              console.log("No such document");
            }
          }).catch(function(error){
            console.log(error)
          })
            console.log(user.displayName + " signed in." + uid)   
        }else {
            console.log("No User signed in")
        }
    })
  },[]);
  

return (
  
  <div className="app">
    <Router>
      <Header user={user.photoURL}/>
      <div className="section">
    <Switch>
    <Route exact path="/products" component={()=>(<Products products={products} loading={loading}/>)}/>
    <Route path="/product/:id" component={() => (<Details products={products} />)}/>
    <Route path="/products/q=:id" component={()=> (<Search products={products}/>)}/>
    <Route path="/app" component={Getapp}/>
    <Route path="/profile/edit" component={() => (<ProfileEdit userData={user}/>)}/>
    <Route path="/profile" component={()=>(<Profile loading={loading} user={user} />)}/>
    <Route path="/about" component={About}/>
    </Switch>
    </div>
    <Footer/>
    </Router>
  </div>
);
}
export default App;